<template>
  <div class="p-2 pt-4">
    <!-- <b-row>
      <b-col
        ><b-button
          @click="showReduction"
          variant="primary"
          class="ReductionBtn"
          >{{ $t("ReductionButton") }}</b-button
        ></b-col
      >
    </b-row> -->
    <br />
    <b-card no-body>
      <div id="settingTable">
        <b-table
          ref="selectableTable"
          :select-mode="selectModeTable"
          :fields="fieldss"
          :items="expenseReductionData"
          :per-page="limit"
          :total="total"
          :startPage="start"
          @change-page="pageChange"
          @onclick="click"
          :busy="isBusy"
           :striped="true"
            :bordered="true"
            :borderless="true"
            head-variant="dark"
            fixed
            sticky-header
            show-empty
        >
          <template v-slot:cell(checkState)="item">
            <b-form-checkbox
              :id="'checkbox-' + item.index"
              v-model="item.item.checkState"
              :name="'checkbox-' + item.index"
              @change="costToggleSelectAll(item)"
            ></b-form-checkbox>
          </template>
          <template v-slot:cell(Type)="item">{{ item.value }}</template>
          <template v-slot:cell(ReductionproposalNo)="item">{{
            item.value
          }}</template>
          <template v-slot:cell(Supplier)="item">{{ item.value }}</template>
          <template v-slot:cell(Reductionproposalname)="item">{{
            item.value
          }}</template>
          <template v-slot:cell(Proposedamount)="item">{{
            item.value
          }}</template>
          <template v-slot:cell(Account)="item">{{ item.value }}</template>
          <template v-slot:cell(Expense)="item">{{ item.value }}</template>
          <template v-slot:cell(CreateDate)="item">{{ item.value }}</template>
          <template v-slot:cell(ProcessStatus)="item">{{
            item.value
          }}</template>
          <template v-slot:cell(ProcessDate)="item">{{ item.value }}</template>
          <template v-slot:cell(PersonIncharge)="item">{{
            item.value
          }}</template>
          <template v-slot:empty>
              <div class="text-center errorColor">
                {{ $t("GENRALDATAERRORMESSAGE") }}
              </div>
            </template>
          <template v-slot:table-busy>
            <div class="text-center text-danger my-2">
              <b-spinner class="align-middle"></b-spinner>
            </div>
          </template>
        </b-table>
      </div>
    </b-card>
    <b-row>
      <b-col cols="12" xl="4">
        <b-button
          :disabled="checkedItems.length != 1"
          @click="updateDeleteFlag"
          variant="primary"
          class="ReductionBtn"
          >{{ $t("Delete") }}</b-button
        >
      </b-col>
      <b-col cols="12" xl="5" class="">
        <b-pagination
          v-model="currentPage"
          :total-rows="totalRows"
          :per-page="limit"
          align="center"
          size="sm"
        >
          <template v-slot:prev-text>{{ $t("PREVIOUS") }}</template>
          <template v-slot:next-text>{{ $t("NEXT") }}</template>
          <template v-slot:page="{ page, active }">
            <strong v-if="active">{{ page }}</strong>
            <i v-else>{{ page }}</i>
          </template>
        </b-pagination>
      </b-col>
      <b-col cols="12" xl="3" class="pr-4 mt-0">
        <div class="countList">
          <label>{{ $t("MEANS") }}</label
          >&nbsp;&nbsp;
          <template v-for="(page, index) in pageOptions">
            <b-link :key="index" v-if="limit == page" href="javascript:;">
              {{ page }}
              <span v-if="index != pageOptions.length - 1">&nbsp;|&nbsp;</span>
            </b-link>
            <b-link
              :key="index"
              @click="perPageChange(page)"
              v-else
              href="javascript:;"
            >
              <u>{{ page }}</u>
              <span v-if="index != pageOptions.length - 1">&nbsp;|&nbsp;</span>
            </b-link>
          </template>
        </div>
      </b-col> </b-row
    ><br />
  </div>
</template>
<script>
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { mapGetters } from "vuex";
import { getDateHour } from "@/shared/utils";
import { endPoint } from "@/shared/endPoint";

export default {
  data() {
    return {
      expenseReductionData: [],
      selectModeTable: "single",
      selected: [],
      isBusy: false,
      proposalType: "",
      statusType: "",
      total: 0,
      limit: 50,
      start: 0,
      currentPage: 1,
      pageOptions: [50, 100, 200],
      checkedItems: [],
      messageFlag: false,
      messageFlag2: false,
      quotationData: [],
      reductionData: [],
    };
  },
  computed: {
    ...mapGetters({
      user_details: "getUserDetails",
      client_details: "getClientData",
    }),
    totalRows() {
      if (this.total) {
        return this.total;
      }
      return this.getRowCount();
    },
    items() {
      return this.expenseReductionData;
    },
    rows() {
      return this.expenseReductionData.length;
    },
    lists() {
      const items = this.expenseReductionData;
      return items.slice(
        (this.currentPage - 1) * this.perPage,
        this.currentPage * this.perPage
      );
    },
    fieldss() {
      return [
        {
          key: "checkState",
          label: "",
          class: "text-center normal-width",
        },
        {
          key: "Type",
          label: this.$t("Type"),
          class: "text-center normal-width",
        },
        {
          key: "ReductionproposalNo",
          label: this.$t("proposalNo"),
          class: "text-center normal-width",
        },
        {
          key: "Supplier",
          label: this.$t("Supplier"),
          class: "text-center normal-width",
        },
        {
          key: "Reductionproposalname",
          label: this.$t("proposalname"),
          class: "text-center normal-width",
        },

        {
          key: "Proposedamount",
          label: this.$t("Proposedamount"),
          class: "text-center normal-width",
        },
        {
          key: "Account",
          label: this.$t("Account"),
          class: "text-center normal-width",
        },
        {
          key: "Expense",
          label: this.$t("Expense"),
          class: "text-center normal-width",
        },
        {
          key: "CreateDate",
          label: this.$t("CreateDate"),
          class: "text-center normal-width",
        },
        {
          key: "ProcessStatus",
          label: this.$t("ProcessStatus"),
          class: "text-center normal-width",
        },
        {
          key: "ProcessDate",
          label: this.$t("ProcessDate"),
          class: "text-center normal-width",
        },
        {
          key: "PersonIncharge",
          label: this.$t("PersonIncharge"),
          class: "text-center normal-width",
        },
      ];
    },
  },
  watch: {
    currentPage() {
      this.pageChange(this.currentPage);
    },
    startPage() {
      this.currentPage = this.startPage + 1;
    },
  },
  mounted() {
    this.getExpenseReductionData();
    this.getQuotationData();
  },
  created() {},
  methods: {
    /**
     * @description function to get the row count
     * @name getRowCount
     */
    getRowCount: function () {
      return this.items.length;
    },
    /**
     * @description function to fetch details on changing the page
     * @name pageChnge
     */
    pageChange(page) {
      this.start = page - 1;
      this.getExpenseReductionData();
    },
    /**
     * @description function to fetch details on changing the page
     * @name perPageChnge
     */
    perPageChange(page) {
      this.limit = page;
      this.getExpenseReductionData();
    },
    /**
     * @description function to fetch the row data on checking
     * @name costToggleSelectAll
     */
    costToggleSelectAll(item) {
      item.item.checkState = !item.item.checkState;
      this.checkedItems = this.expenseReductionData.filter((checkedItem) => {
        if (checkedItem.checkState == true) {
          return checkedItem;
        }
      });
    },
    /**
     * @description function to fetch details of expenseReduction based on the client_no
     * @name getExpenseReductionData
     */
    async getExpenseReductionData() {
      this.isBusy = true;
      const { data } = await BasicInfoApis.postAPI(
        endPoint.CLIENTREADREDUCTIONPROPOSALLIST,
        {
          client_no: 20,
          limit: this.limit,
          offset: this.start * this.limit,
          paginate: true,
        }
      );
      if (data.success) {
        this.total = 0;
        let response = data.data.reductionProposalList;
        let responseData;
        if (response.length > 0) {
          // responseData = this._.sortBy(response, "create_dt").reverse();
          responseData = response;
          this.total = data.data.total;
          let expenseReductioninfo = [];
          responseData.forEach((item) => {
            if (item.proposal_type === "001") {
              this.proposalType = this.$t("ProposalType1");
            } else if (item.proposal_type === "002") {
              this.proposalType = this.$t("ProposalType2");
            } else if (item.proposal_type === "000") {
              this.proposalType = this.$t("ProposalType3");
            } else {
              this.proposalType = "";
            }
            if (item.sts_kbn == "01") {
              this.statusType = this.$t("Prossesing");
            } else if (item.sts_kbn == "02") {
              this.statusType = this.$t("UnProcessed");
            } else if (item.sts_kbn == "03") {
              this.statusType = this.$t("Processed");
            }
            let Amount;
            if (item.reduction_proposal_cost == null) {
              Amount = null;
            } else {
              Amount =item.reduction_proposal_cost
            }
            let expenseReductiontableInfo = {
              checkState: false,
              Type: this.proposalType,
              ReductionproposalNo: item.reduction_proposal_no,
              Supplier: item.supplier_name,
              Reductionproposalname: item.reduction_proposal_name,
              Proposedamount: Amount,
              Account: item.accounts_name_nm,
              Expense: item.expense_item_nm,
              CreateDate: getDateHour(new Date(item.create_dt)),
              ProcessStatus: this.statusType,
              ProcessDate: getDateHour(new Date(item.update_dt)),
              PersonIncharge: item.person_name,
              Icon: "1",
              request_quotation_no: item.request_quotation_no,
            };
            expenseReductioninfo.push(expenseReductiontableInfo);
          });
          this.expenseReductionData = expenseReductioninfo;
          this.isBusy = false;
        } else {
          this.isBusy = false;
        }
      }
    },
    /**
     * @description function to convert numbrt to currency format
     * @name filterByCuurency
     */
    filterByCurrency(value) {
      try {
        return parseInt(value)
          .toString()
          .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
      } catch {
        return 0;
      }
    },
    /**
     * @description function to update the del_flag based on the reductionProposalNo
     * @name updateDelFlg
     */
    async updateDelFlg() {
      let compileData = this.checkedItems;
      let reductoNoArray = [];
      let qutationNoArray = [];
      compileData.forEach((item) => {
        let reductionNo = item.ReductionproposalNo;
        let quotationNo = item.request_quotation_no;
        reductoNoArray.push(reductionNo);
        qutationNoArray.push(quotationNo);
      });
      const { data } = await BasicInfoApis.postAPI(
        endPoint.CLIENTDELETEREDUCTIONPROPOSALLIST,
        {
          reduction_proposal_no: reductoNoArray,
          request_quotation_no: qutationNoArray,
          client_no: 20,
          update_id : localStorage.getItem("personID")
        }
      );
      if (data.success) {
        this.getExpenseReductionData();
      }
    },
    /**
     * @description function to check exisitence of reductonProposalNo
     * @name updateDeleteFlag
     */
    async updateDeleteFlag() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADDATATREDUCTIONPROPOSAL,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "client_no",
              value: 20,
            },
          ],
        }
      );
      this.reductionData = data.data;
      let compileData = this.checkedItems;
      let reductoNo = "";
      let self = this;
      compileData.forEach((item) => {
        reductoNo = item.ReductionproposalNo;
      });
      this.reductionData.forEach((item) => {
        if (reductoNo == item.reduction_proposal_no) {
          this.quotationData.forEach((item) => {
            if (reductoNo == item.reduction_proposal_no) {
              this.messageFlag = true;
            }
          });
        }
      });
      let successHtml = this.getSuccessHtml();
      this.$swal({
        width: 456,
        html: successHtml,
        showCancelButton: true,
        confirmButtonText: "OK",
        cancelButtonText: this.$t("CANCEL"),
        cancelButtonColor: "#062854",
        cancelButtonClass: "btn-primary",
      }).then(async function (isconfirm) {
        if (!isconfirm.value == true) {
          self.messageFlag = false;
          self.messageFlag2 = false;
        } else {
          self.checkQuotation();
        }
      });
    },
    /**
     * @description function to check existence  of quotationNo
     * @name checkQuotation
     */
    async checkQuotation() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADDATATAGREEMENT,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "client_no",
              value: 20,
            },
          ],
        }
      );
      let responseData = data.data;
      let compileData = this.checkedItems;
      let qutationNoArray = "";
      compileData.forEach((item) => {
        qutationNoArray = item.request_quotation_no;
      });
      responseData.forEach((item) => {
        if (qutationNoArray == item.request_quotation_no) {
          this.quotationData.forEach((item) => {
            if (qutationNoArray == item.request_quotation_no) {
              this.messageFlag2 = true;
            }
          });
        }
      });
      let successHtml = this.successHtml();
      let self = this;
      this.$swal({
        width: 456,
        html: successHtml,
        showCancelButton: true,
        confirmButtonText: "OK",
        cancelButtonText: this.$t("CANCEL"),
        cancelButtonColor: "#062854",
        cancelButtonClass: "btn-primary",
      }).then(async function (isconfirm) {
        if (!isconfirm.value == true) {
          self.messageFlag = false;
          self.messageFlag2 = false;
          return;
        } else {
          self.updateDelFlg();
        }
      });
    },
    /**
     * @description function to fetch the quotationData
     * @name getQuotationData
     */
    async getQuotationData() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADDATATREQUESTQUOTATION,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "client_no",
              value: 20,
            },
          ],
        }
      );
      this.quotationData = data.data;
    },
    click(item) {
      console.log("values", item);
    },
    /**
     * @description function to set popup data
     * @name getSuccessHtml
     */
    getSuccessHtml() {
      if (this.messageFlag == true) {
        let successHtml = `<div >` + this.$t("DELFLAGMESSAGE1") + `</div> `;
        successHtml += `<div>` + this.$t("DELFLAGMESSAGE2") + `</div> `;
        successHtml += `<div>` + this.$t("DELFLAGMESSAGE3") + `</div> `;
        return successHtml;
      } else {
        let successHtml = `<div>` + this.$t("DELFLAGMESSAGE") + `</div> `;
        return successHtml;
      }
    },
    /**
     * @description function to set popup data
     * @name getSuccessHtml
     */
    successHtml() {
      if (this.messageFlag2 == true) {
        let successHtml = `<div >` + this.$t("DELFLAGMESSAGE4") + `</div> `;
        successHtml += `<div>` + this.$t("DELFLAGMESSAGE5") + `</div> `;
        successHtml += `<div>` + this.$t("DELFLAGMESSAGE6") + `</div> `;
        return successHtml;
      } else {
        let successHtml = `<div>` + this.$t("DELFLAGMESSAGE7") + `</div> `;
        return successHtml;
      }
    },
    /**
     * @description function to call requestAppDetails
     * @name editDetails
     */
    editDetails(item) {
      let compileData = item.item.request_quotation_no;
      let proposalNo = item.item.ReductionproposalNo;
      let supplier = item.item.Supplier;
      this.$store.dispatch("setQuotationNo", {
        Quotation_no: compileData,
        Proposal_no: proposalNo,
        Supplier : supplier,
      });
      this.$router.push({
        name: "client_mgmt_request_app_detail",
        params: { data: compileData },
      });
    },
    /**
     * @description function to call reductionProposal page
     * @name showReduction
     */
    showReduction() {
      this.$router.push({ name: "client_mgmt_cost_reduction_proposal" });
    },
  },
};
</script>
<style scoped lang="scss">
$primary: #062854 !default;
$unread_color: #afabab;
$icon_background: red;

.link {
  color: $primary;
  font-weight: 900;
}
.collclass4 {
  border: 1px solid #c8ced3;
}
.ReductionBtn {
  margin-left: 10px;
}
.Detailsbtn {
  padding: 6px 17px 6px 17px !important;
  min-width: 10px !important;
  font-size: 11px;
  border-radius: 0px;
  cursor: default !important;
}
.countList {
  float: right;
  padding-top: 10px;
}
#selectableTable .table thead th {
  vertical-align: bottom;
  border-bottom: none !important;
}
.b-table-sticky-header {
  font-size: 0.75rem !important;
  border: 1px solid #c8ced3;
  margin-bottom: 0px;
}
</style>
